<template>
    <v-select
            :rules="rules"
            :value="value"
            @change="$emit('change', $event)"
            dense
            label=""
            v-bind:items="items"
    ></v-select>
</template>

<script>
    export default {

        name: "AuthTypeSelect",
        props: {
            value: String,
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data: () => ({
            items: [
                {text: '1', value: '1'},
                {text: '2', value: '2'},
                {text: '3', value: '3'}
            ],
            rules: [v => v !== undefined || '']
        })
    }
</script>

